import { Fragment } from "react";

import "./App.css";
import { Header } from "./Components/Header/Header";
import { Home } from "./Components/Inicio/Home";
import { AboutMe } from "./Components/AboutMe/AboutMe";
import { Portfolio } from "./Components/Portfolio/Portfolio";
import { Footer } from "./Components/Footer/Footer";
import { Contact } from "./Components/Contact/Contact";
import { useAddToHomeScreenPrompt } from "./Hooks/useAddToHomeScreenPrompt";

// localStorage.setItem("leng", "ES")

function App() {
  const { isReady, promptToInstall, hasFinishedInstallation } =
    useAddToHomeScreenPrompt();

  return (
    <Fragment>

      <Header />

      
      <main>
        <Home />
      <div className="App">
        <h1> Latte and Code</h1>
        <p>Instala esta aplicacin en tu dispositivo</p>
        <p>
          {isReady && !hasFinishedInstallation && (
            <button onClick={() => promptToInstall()}>Instalar</button>
          )}
        </p>
        <p>
          {hasFinishedInstallation
            ? "Ests usando la Web App"
            : "Aplicacin todava no instalada"}
        </p>
      </div>

        <AboutMe />

        <Portfolio />

        <Contact />

        <Footer />
      </main>
    </Fragment>
  );
}

export default App;
